<template>
<div style="background:#F3F3F3">
    <van-image :src="content.image.file_path" />
    <div class="lesson-block">
        <div class="lesson-title">{{content.title}}</div>
 
        <div class="lesson-tags">
            <!-- 课程分类 -->
            <van-tag color="#DCAA81" plain round type="primary" size="medium">{{ content.cate.title }}</van-tag>
            <!-- 是否公开 -->
            <van-tag v-if=" content.is_public.value == 1" plain round type="danger" size="medium">{{ content.is_public.text }}</van-tag>
            <!-- 大咖私享 -->
            <van-tag v-if=" content.is_private.value == 1" plain round type="primary" size="medium">{{ content.is_private.text }}</van-tag>
            <!-- 级别可见 -->
            <van-tag v-if=" content.is_grade.value == 1" plain round type="success" size="medium">{{ content.is_grade.text }}</van-tag>

        </div>
        <div class="lesson-desc">{{ content.desc }}</div>
        <div class="lesson-num">已开课{{ content.class_num }}节| 总共{{ content.total_size }}节课</div>
    </div>

    <div class="lesson-teacher">
        <van-cell center :title="content.lecturer.name">
            <template #label>
                <div class="lecturer-tags">
                    <van-tag v-for="item in content.lecturer.label_list" :key="item"  color="#DCAA81" plain round type="success" style="margin-right: 6px;">{{item}}</van-tag>
                </div>
            </template>
            <template #icon>
                <div style="margin-right:10px;margin-top:4px">
                <van-image
                    round
                    width="3.5rem"
                    height="3.5rem"
                    :src="content.lecturer.image.file_path"
                    />
                </div>
            </template>
        </van-cell>
    </div>

    <div class="lesson-content">
    <van-tabs  v-model="active" color="#DCAA81">
        <van-tab title="课程介绍" name="a">
            <div class="lesson-content-detail" v-html="content.content"></div>
        </van-tab>
        <van-tab title="课程章节">
            <div style="padding-bottom: 60px">
                      <van-list>
            <van-cell v-for="item in content.class_list" :key="item.class_id">
                <template slot="default">
                    <router-link :to="{name: 'content',query: {class_id: item.class_id}}">
                    <van-card
                        :thumb="item.image.file_path"
                        :desc="item.desc"
                        >
                        <template #title>
                            <div class="lesson-title">{{item.title}}</div>
                        </template>

                        <template #price style="width:100%">
                            <span class="read-num">已有{{ item.watch_num }}个人学习过</span>
                        </template>
                    </van-card>
                    </router-link>
                </template>
            </van-cell>
                      </van-list>
            </div>
        </van-tab>
    </van-tabs>

    </div>


</div>

 
</template>

<script>
import Vue from 'vue';
import { Cell, CellGroup } from 'vant';

Vue.use(Cell);
Vue.use(CellGroup);


export default {
    name: 'series',
    props: {},
    data(){
        return{
            active: 0,
            content: {
                image: '',
                cate: '',
                is_public: '',
                is_private: '',
                is_grade: '',
                lecturer: {
                    image: ''
                }
            },
            class_list:{
                image: ''
            }
        }
    },
    mounted(){
        this.getLessonContent()
    },
    methods:{
        getLessonContent(){
            let _this = this;
            _this.https.get('college.lesson/lessonDetail',_this.$route.query).then(res=>{
                _this.content = res.data.info;
          })
        }

    }

}
</script>

