<template>
<div style="background:#F3F3F3">
    <div class="banner-container" v-show="showBannerStatus">
         <van-image style="z-index:" :src="playerOptions.poster" />
         <van-icon @click="checkAccess" style="position:absolute;z-index:9" name="play-circle" size="46"></van-icon>
    </div>
   
    <video-player v-show="!showBannerStatus" class="video-player vjs-custom-skin"
        ref="videoPlayer" 
        :playsinline="true" 
        :options="playerOptions"
        @play="onPlayerPlay($event)"
        customEventName="customstatechangedeventname"
    ></video-player>
    <div class="lesson-block">
        <div class="lesson-title">123123</div>
 
        <div class="lesson-tags">
            <!-- 课程分类 -->
            <van-tag color="#DCAA81" plain round type="primary" size="medium">{{ info.lesson.cate.title }}</van-tag>
        </div>
        <div class="lesson-desc">{{ info.desc }}</div>
        <!-- <div class="lesson-num">已开课{{ content.class_num }}节| 总共{{ content.total_size }}节课</div> -->
    </div>

    <div class="lesson-teacher">
        <van-cell center :title="info.lesson.lecturer.name">
            <template #label>
                <div class="lecturer-tags">
                    <van-tag v-for="item in info.lesson.lecturer.label_list" :key="item"  color="#DCAA81" plain round type="success" style="margin-right: 6px;">{{item}}</van-tag>
                </div>
            </template>
            <template #icon>
                <div style="margin-right:10px;margin-top:4px">
                <van-image
                    round
                    width="3.5rem"
                    height="3.5rem"
                    :src="info.lesson.lecturer.image.file_path"
                    />
                </div>
            </template>
        </van-cell>
    </div>

    <div class="lesson-content">
    <van-tabs  v-model="active" color="#DCAA81">
        <van-tab title="课程介绍" name="a">
            <div class="lesson-content-detail" v-html="info.content"></div>
        </van-tab>
    </van-tabs>

    </div>

    <van-dialog v-model="show" title="请输入邀请码" show-cancel-button :beforeClose="confirmBtn">
        <van-cell-group>
            <van-field v-model="codeValue" placeholder="请输入邀请码" />
        </van-cell-group>
    </van-dialog>

</div>

 
</template>

<script>
import Vue from 'vue';
import { Cell, CellGroup, Toast } from 'vant';
import VueVideoPlayer from 'vue-video-player'
import { Dialog } from 'vant';
import { Field } from 'vant';


require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')

Vue.use(VueVideoPlayer)
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Dialog);
Vue.use(Field);

export default {
    name: 'cont',
    props: {},
    data(){
        return{
            show: false,
            active: 0,
            info: {
                lesson:{
                    cate: '',
                    lecturer:{
                        image: ''
                    },
                    lesson_type: ''
                }
            },
            playerOptions : {
                playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
                autoplay: false, //如果true,浏览器准备好时开始回放。
                muted: false, // 默认情况下将会消除任何音频。
                loop: false, // 导致视频一结束就重新开始。
                preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                language: 'zh-CN',
                aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                sources: [{
                    type: "",//这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
                    src: "" //url地址
                }],
                poster: "", //你的封面地址
                width: document.documentElement.clientWidth, //播放器宽度
                notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
                controlBar: {
                    timeDivider: true,
                    durationDisplay: true,
                    remainingTimeDisplay: false,
                    fullscreenToggle: true  //全屏按钮
                }
            },
            showBannerStatus: true, //1展示，2不展示 
            codeValue: ''
        }
    },
    mounted(){
         console.log('this is current player instance object', this.player)
        this.getLessonContent()
    },
    computed: {
      player() {
        return this.$refs.videoPlayer.player
      },

      playsinline(){
        var ua = navigator.userAgent.toLocaleLowerCase();
        if (ua.match(/tencenttraveler/) != null || ua.match(/qqbrowse/) != null) {
            return false
        }else{
            return true             
        }
      }
    },
    methods:{
        onPlayerPlay(player) {
            return this.$refs.videoPlayer.player
        },
        getLessonContent(){
            let _this = this;
            _this.https.get('college.lesson/classDetail',_this.$route.query).then(res=>{
                _this.info = res.data.info;
              
                _this.playerOptions.sources[0].src = res.data.info.video_url;
                _this.playerOptions.poster = res.data.info.image.file_path;
                // console
          })
        },
        checkAccess(){
            let _this = this;
                     _this.$route.query.class_code = '';
             _this.https.post('college.lesson/checkAccess',_this.$route.query).then(res=>{
                console.log(res);
                // console
                if(res.code == 1){
                    _this.showBannerStatus = false;
                }
                else if(res.code == 2){
                    _this.show = true;

                }
                else{
                    Toast(res.msg);
                }
          })
        },
        confirmBtn(action, done){
            if (action === 'cancel') {//取消按钮
                done();
            } else if (action === 'confirm') {//确定按钮
                //向后端传值并关闭dialog弹出框
                
                if(this.codeValue == ''){
                    Toast('邀请码不能为空哦');
                    done(false);
                }else{
                    this.checkCode();
                    this.codeValue = '';
                    done();
                }
            
          };
           
            // done();
        },
        checkCode(){
            let _this = this;
            console.log(_this.codeValue);
            _this.$route.query.class_code = _this.codeValue;
            console.log(_this.$route.query);
             _this.https.post('college.lesson/checkAccess',_this.$route.query).then(res=>{
                console.log(res);
                // console
                if(res.code == 1){
                    _this.showBannerStatus = false;
                }
                else{
                    _this.codeValue = '';
                    Toast(res.msg);
                }

          })
        }
        // onPlayerPlay(player) {
        //     this.videoPlayState = true;
        // }

    }

}
</script>

