  .vjs-custom-skin > .video-js .vjs-big-play-button{
      font-size: 1.5rem;
  }
  .banner-container{
      display: flex;
      align-items: center;
      justify-content: center;
  }
  .lesson-block{
      padding: 10px;
      background: #fff;
  }
  .lesson-title{
      
  }
  .lesson-desc{
      font-size: 12px;
      color: #7E7E7E;
      margin-top: 6px;
      line-height: 1.6
  }
  .lesson-tags{
      margin-top: 6px;
  }
  .lesson-num{
      font-size: 10px;
      color: #333;
      text-align: right;
      margin-top: 6px;
  }
  .lesson-teacher{
      margin: 10px 0;
  }
  .lesson-content{
      background: #fff;
  }
  .lecturer-tags{
      display: flex!important;

  }
    .lesson-content-detail{
        padding-bottom: 30px;
    }
  .lesson-content-detail img{
      width: 100%;
  }
