<template>
<div>
        <van-search
  v-model="searchValue"
  shape="round"
  background="#fff"
  placeholder="请输入搜索关键词"
   @search="onSearch"
/>
    <div v-if="lessonList.length>0">
    <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多课程了"
        @load="onLoad()"
        >

        <van-cell v-for="item in lessonList" :key="item.lesson_id">
            <template slot="default">
            <van-card
                :thumb="item.image.file_path"
                :tag="item.lesson_type.text"
                @click="toLesson(item)"
                >
                <template #title>
                    <div class="lesson-title">{{item.title}}</div>
                </template>
                <template #tag>
                    <van-tag mark type="primary">{{ item.lesson_type.text }}</van-tag>
                </template>
                <template #tags>
                    <!-- <van-tag round color="#DCAA81" plain>{{ item.lesson_type.text }}</van-tag> -->
                    <van-tag round color="#DCAA81" plain>{{ item.cate.title }}</van-tag>
                </template>
                <template #price style="width:100%">
                    <span class="read-num">已有{{ item.watch_num }}个人看过</span>
                </template>
            </van-card>
            </template>
        </van-cell>
     
    </van-list>
    </div>
     <div v-else>
        <van-empty description="没有搜索到相关课程哦" />
    </div>
  
</div>
</template>

<script>
export default {
    name:'search',
    data(){
        return{
            searchValue: '',
            list: [],
            loading: false,
            finished: false,
            lessonList: [],
            page: 1,
            size: 6
        }
    },
    props: {},
    created(){
        this.keywords = this.$route.query.keywords;
        this.getSearchList();
    },
    mounted(){
       
        this.searchValue = this.$route.query.keywords

    },
    methods:{
        getSearchList(){
           let _this = this;
            _this.https.get('college.lesson/search',{
                keywords: _this.keywords,
                page: _this.page,
                size: _this.size
            }).then(res=>{
                    let resList = res.data.list;
                    console.log(resList);
                    _this.loading = false;
    
                    _this.lessonList = _this.lessonList.concat(resList);
                    
                    if (resList == null || resList.length === 0 || resList.length< _this.size) {
                        // 加载结束
                        _this.finished = true;
                        return;
                    }
                    //如果列表数据条数>=总条数，不再触发滚动加载
                    // if (_this.lessonList.length >= _this.total) {
                    //     _this.finished = true;
                    // }
            }) 
        },
        onLoad() {
            // 异步更新数据
            this.page++;
            this.getSearchList();
        },
         onSearch(val) {
            console.log(val);
            this.keywords = val;
            this.page = 1;
            this.size = 6;
            this.lessonList = [];
            this.getSearchList();
            
        },
    }
}
</script>>
