import axios from 'axios'
import $qs from 'qs'

axios.defaults.baseURL ='https://168.dekichina.com/index.php?s=/api/';


//请求拦截
axios.interceptors.request.use(
    config => {
      // config.data = JSON.stringify(config.data);
    if(config.method=='post'){
        config.data = {
            ...config.data,
            wxapp_id: '10001'
        }
    }else if(config.method=='get'){
        config.params = {
            wxapp_id: '10001',
            ...config.params
        }
    }
      config.headers = {
        'Content-Type':'application/json'
      };
      
      return config;
    },
    error => {
      return Promise.reject(err);
    }
  );

  //响应拦截
  axios.interceptors.response.use(
    response => {
      return response;
    },
    error => {
      return Promise.reject(error)
    }
  )

  //请求函数

  export default {
    post(url,data={}){
      return new Promise((resolve,reject) => {
        axios.post(url,data)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err)
          })
      })
    },
    get(url,params={}){
      return new Promise((resolve,reject) => {
        axios.get(url,{
          params:params
        })
          .then(response => {
            resolve(response.data);
          })
          .catch(err => {
            reject(err)
          })
      })
    }
  }
