<template>
<div>
    <van-search
  v-model="searchValue"
  shape="round"
  background="#fff"
  placeholder="请输入搜索关键词"
   @search="onSearch"
/>
<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
    <!-- <router-link to="/list">
  <van-swipe-item>1</van-swipe-item>
  </router-link> -->
  <van-swipe-item v-for="item in bannerList" :key="item.index">
      <van-image :src="item.imgUrl"/>
  </van-swipe-item>
</van-swipe>

<van-tabs v-model="active" color="#DCAA81" style="padding-bottom: 60px">
  <van-tab v-for="item in cateList" :key="item.lesson_cate_id" slots="secCat">
        <template #title>
            <div @click="getCatPid(item.lesson_cate_id)">{{ item.title }}</div>
        </template>
        <template name="secCat">
            <div v-show="item.child.length != 0" class="child-cat">
                <van-tag v-for="child in item.child" :key="child.lesson_cate_id" :plain="activeClass==child.lesson_cate_id?false:true" round type="primary" :color="activeClass==child.lesson_cate_id?'#DCAA81':'#DCAA81'" @click="getCatId(child.lesson_cate_id)" style="height: 26px;padding: 0 10px;margin-left:8px">{{child.title}}</van-tag>
            </div>
        </template>
    <!-- <div v-if="lessonList.length>0"> -->
        <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多课程了"
        @load="onLoad()"
        >

        <van-cell v-for="item in lessonList" :key="item.lesson_id">
            <template slot="default">
            <van-card
                :thumb="item.image.file_path"
                :tag="item.lesson_type.text"
                @click="toLesson(item)"
                >
                <template #title>
                    <div class="lesson-title">{{item.title}}</div>
                </template>
                <template #tag>
                    <van-tag mark type="primary">{{ item.lesson_type.text }}</van-tag>
                </template>
                <template #tags>
                    <!-- <van-tag round color="#DCAA81" plain>{{ item.lesson_type.text }}</van-tag> -->
                    <van-tag round color="#DCAA81" plain>{{ item.cate.title }}</van-tag>
                </template>
                <template #price style="width:100%">
                    <span class="read-num">已有{{ item.watch_num }}个人看过</span>
                </template>
            </van-card>
            </template>
        </van-cell>
     
        </van-list>
    <!-- </div> -->

    
    <!-- <div v-else>
        <van-empty description="该分类还没有课程哦" />
    </div> -->
  
  </van-tab>

</van-tabs>
</div>

</template>

<script>
import Vue from 'vue';
import { Swipe, SwipeItem, Toast } from 'vant';
import { Tab, Tabs } from 'vant';
import { Search } from 'vant';

Vue.use(Search);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Swipe);
Vue.use(SwipeItem);

export default {
    name: 'index',
    props: {},
    data(){
        return{
            bannerList: [],
            searchValue: '',
            active: 0,
            list: [],
            total: '',
            loading: false,
            finished: false,
            cateList: [],
            catPid: 0, //获取一级分类id
            catId: '', //二级分类id
            page: 1,
            lessonList: [],
            activeClass: ''
        } 
    },
    created(){
        this.getLessonList();
    },
    mounted(){
        this.getBannerList();
        this.getCateList();
       
    },
    methods:{
        getBannerList(){
            let _this = this;
            _this.https.get('page/index',{
                page_id: 10003 
            }).then(res=>{
          
                _this.bannerList = res.data.items[0].data;
            })
        },
        getCatPid(data){
            // 获取一级
            this.catPid = data;
            this.catId = 0;
            this.page = 1;
            this.lessonList = [];
            this.getLessonList();
        },
        getCatId(id){
            // 获取二级
            this.activeClass = id;
            this.catId = id;
            this.page = 1;
            this.lessonList = [];
            this.getLessonList();
        },
        getCateList(){
            let _this = this;
            _this.https.get('college.lesson/cateList').then(res=>{
                let jsonList = res.data.cate_list;
                let arrCat = {
                   child: [],
                    is_show: 1,
                    lesson_cate_id: 0,
                    pid: 0,
                    sort: 9999,
                    title: "推荐"
                }
                jsonList['0']=arrCat;
                _this.cateList = jsonList;
          })

        },
        getLessonList(){
            let _this = this;
            _this.https.get('college.lesson/lessonList',{
                    cate_pid: _this.catPid||0, //一级分类id 推荐填写0
                    cate_id: _this.catId || 0, //二级分类id 全部填写0
                    page: _this.page,
                    size: 6
                }).then(res=>{
                    let resList = res.data.list.data;
                    _this.loading = false;
                    _this.total = res.data.list.total; 

                    if (resList == null || resList.length === 0) {
                        // 加载结束
                        _this.finished = true;
                        return;
                    }
                    _this.lessonList = _this.lessonList.concat(resList);
                    
                    //如果列表数据条数>=总条数，不再触发滚动加载
                    if (_this.lessonList.length >= _this.total) {
                        _this.finished = true;
                    }
            })
        },
        onLoad() {
            // 异步更新数据
            this.page++;
            this.getLessonList();
        },
        toLesson(item){
      
            // let _this =this;
            if(item.lesson_size.value == 20){
                //多课程
                this.$router.push({path:'series',query:{lesson_id: item.lesson_id}});
            }else if(item.lesson_size.value == 10){
                //单课程
                this.$router.push({path:'content',query:{class_id: item.lesson_class.class_id}});
            }
        },
         onSearch(val) {
            console.log(val);
            if(val){
                this.$router.push({path:'search',query:{keywords:val}});
            }else{
                Toast("请输入搜索关键词")
            }
            
        },
     }
    
}
</script>>

